/* ResumePreview.css */

/* Main Container */
.resume-preview {
    background-color: #a99f73;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
  }
  
  /* Title */
  .preview-title {
    font-size: 2rem;
    text-align: center;
    color: #070b20;
    margin-bottom: 20px;
    border-bottom: 2px solid #e0e0e0;
    padding-bottom: 10px;
  }
  
  /* Section Headings */
  .section-heading {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
  }

  .profile-image {
    margin-bottom: 15px;
    text-align: center;
  }
  
  .profile-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #ddd;
  }







  
  /* Section Content */
  .personal-info p,
  .experience ul,
  .education ul,
  .skills ul {
    margin: 10px 0;
    font-size: 1rem;
    color: #555;
  }
  
  /* Placeholders for Empty Sections */
  .placeholder {
    color: #612e2e;
    font-style: italic;
  }
  
  /* Experience Items */
  .experience-item,
  .education-item {
    margin-bottom: 15px;
  }
  
  .item-title {
    font-weight: bold;
    color: #1b1807;
  }
  
  /* Skills List */
  .skills-list {
    list-style: none;
    padding: 0;
  }
  
  .skill-item {
    display: inline-block;
    background-color: #f0f0f0;
    color: #333;
    padding: 5px 10px;
    border-radius: 20px;
    margin: 5px;
    font-size: 0.9rem;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .resume-preview {
      padding: 15px;
    }
  
    .section-heading {
      font-size: 1.3rem;
    }
  
    .item-title {
      font-size: 1rem;
    }
  
    .skills-list {
      font-size: 0.8rem;
    }
  }
  