/* App.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-image: url('D:\resumebuilder app\resume-builder\bg.jpg');
  background-size: cover; /* This ensures the image covers the whole screen */
  background-position: center center; /* Centers the image */
  background-attachment: fixed; /* Ensures the background remains static while scrolling */
  
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  background: rgba(81, 55, 19, 0.929);  /* Optional: white background with some transparency */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 800px;
  width: 100%;
  color: #e8e3e3;
}

