/* App.css */

/* Button styling for the introduction section */
button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

/* Introduction Section */
.introduction {
  background-color: #f4f4f4;
  padding: 15px;
  border-radius: 5px;
  margin-top: 20px;
}

.introduction h3 {
  font-size: 20px;
  font-weight: bold;
}

.introduction p {
  font-size: 16px;
  line-height: 1.5;
  color: #333;
}

/* Success and error message styling */
.success-message {
  color: green;
  font-weight: bold;
  margin-top: 10px;
}

.error-message {
  color: red;
  font-weight: bold;
  margin-top: 10px;
}



