/* PersonalInfo.css */

/* Container Styling */
.personal-info-section {
    background: #e2a570;
    border: 1px solid #ecebeb;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  /* Title Styling */
  .section-title {
    color: #dfe5eb;
    font-size: 1.8rem;
    margin-bottom: 20px;
    border-bottom: 2px solid #ddd;
    padding-bottom: 5px;
  }
  
  /* Form Group Styling */
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }
  
  .form-group input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .form-group input:focus {
    outline: none;
    border-color: #2196F3;
    box-shadow: 0 0 5px rgba(198, 207, 215, 0.5);
  }

  .image-preview {
    margin-top: 10px;
  }
  
  .preview-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 50%;
    border: 2px solid #ccc;
  }





  
  /* Button Styling */
  .save-button {
    background-color: #453d09;
    color: #fff;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .save-button:hover {
    background-color: #e0e5eb;
  }
  
  .save-button:active {
    background-color: #6893c4;
  }
  